<template>
  <div class="layout">
    <div :style="{width: this.isCollapsed?'64px':'200px'}" class="siderbar-wrapper">
      <div class="logo-wrapper">logo</div>
      <el-menu
        :collapse="isCollapsed"
        :default-active="$route.path"
        active-text-color="#65cea7"
        background-color="#424f63"
        class="menu-wrapper"
        router
        text-color="#fff"
        unique-opened
      >
        <template v-for="(item, index) in sider_menu_data">
          <el-menu-item :index="item.path" :key="index" class="menu-item" v-if="!item.children">
            <i :class="item.icon"></i>
            <span slot="title">{{item.title}}</span>
          </el-menu-item>
          <el-submenu :index="item.path" v-else>
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{item.title}}</span>
            </template>
            <el-menu-item
              :index="sub_item.path"
              :key="sub_index"
              class="menu-item"
              v-for="(sub_item, sub_index) in item.children"
            >
              <span slot="title" style="margin-left:12px;">{{sub_item.title}}</span>
            </el-menu-item>
          </el-submenu>
        </template>
      </el-menu>
    </div>
    <div :style="{left: this.isCollapsed?'64px':'200px'}" class="topbar-wrapper">
      <div @click="toggleMenu" class="menu-collapse-wrapper float-left">
        <i
          :style="{transform: 'rotateZ(' + (this.isCollapsed ? '90' : '0') + 'deg)'}"
          class="el-icon-adm-menu"
        ></i>
      </div>

      <div class="btn_left">
        <div class="user item">
          <i class="icon el-icon-user"></i>
        </div>

        <!-- 系统通知 -->
        <div class="notice item">
          <div>
            <el-badge :is-dot="this.$store.state.nfDot" style="display:flex;">
              <!-- <i class="fa fa-bell-o" @click="goChat" style="cursor: pointer"></i> -->
              <i @click="goNotice" class="icon el-icon-bell"></i>
            </el-badge>
          </div>
        </div>

        <!-- 注销 -->
        <div @click="logout" class="log_out item">
          <i class="el-icon-switch-button"></i>
        </div>
      </div>
    </div>
    <div
      :style="{left: this.isCollapsed?'64px':'200px'}"
      class="content-wrapper"
      ref="content-wrapper"
    >
      <div class="content">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
import seesionStorage from '../../assets/js/storage/sessionStorage'

export default {
  created () {
    this.checkAuth();
    this.loadNotice();
  },
  data () {
    return {
      sider_menu_data: [
        {
          path: '/home',
          title: '首页',
          icon: 'el-icon-adm-home'
        },
        {
          path: '/examination',
          title: '试题管理',
          icon: 'el-icon-adm-linechart',
          children: [
            { path: '/examination/basic', title: '所有试题' }
        
          ]
        },
        {
          path: '/attendence',
          title: '考勤管理',
          icon: 'el-icon-adm-linechart',
          children: [
            { path: '/attendence/clock', title: '打卡管理' },
            { path: '/attendence/workLog', title: '日志管理' }
          ]
        },
        {
          path: '/department/department',
          title: '部门管理',
          icon: 'el-icon-adm-project'
        },

        {
          path: '/emp',
          title: '员工管理',
          icon: 'el-icon-adm-statistics',
          children: [
            { path: '/emp/basic', title: '基础信息' },
            { path: '/emp/integral', title: '信用分管理' }
          ]
        },
        {
          path: '/charts',
          title: '图表管理',
          icon: 'el-icon-adm-statistics',
          children: [
            { path: '/charts/bar', title: '柱状图' },
            { path: '/charts/line', title: '折线图' },
            { path: '/charts/pie', title: '饼图' }
          ]
        },
        {
          path: '/form',
          title: '表单管理',
          icon: 'el-icon-adm-form',
          children: [{ path: '/form/render', title: '渲染表单' }]
        },
        {
          path: '/system/index',
          title: '系统管理',
          icon: 'el-icon-adm-project'
        },

        {
          path: '/user/index',
          title: '用户管理',
          icon: 'el-icon-adm-user'
        },
        {
          path: '/access/index',
          title: '权限管理',
          icon: 'el-icon-adm-organization'
        },
        {
          path: '/log/index',
          title: '操作日志',
          icon: 'el-icon-adm-log'
        },
        {
          path: '/test/map',
          title: '地图',
          icon: 'el-icon-adm-formsetup'
        }
      ],
      isCollapsed: false,
      adminMenuShow: false
    };
  },
  methods: {
    logout () {
      seesionStorage.setItem('token', '');
      this.$router.push({ path: '/login' })
    },
    loadNotice () {
      var _this = this;
      this.getRequest('/notice/all').then(resp => {
        _this.sysmsgs = resp.data;
        var isDot = false;
        _this.sysmsgs.forEach(msg => {
          if (msg.state === 0) {
            isDot = true;
          }
        })
        _this.$store.commit('toggleNFDot', isDot);
      })
    },
    goNotice () {
      this.$router.push({ path: '/notice' })
    },
    checkAuth () {
      let token = sessionStorage.getItem('token');
      console.log('checkAuth' + token);
      console.log(typeof (token));
      if (token === 'null') {
        console.log('进入if');
        this.$router.replace('/login');
      }
    },
    handleCommand (command) {
      console.log(command);
    },
    toggleMenu () {
      this.isCollapsed = !this.isCollapsed;
    },
    exit () {
      this.$confirm('即将退出系统登陆，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // this.$store.commit('SET_TOKEN', '');
          // this.$store.commit('SET_USER', null);
          this.$router.replace({ path: '/login' });
        })
        .catch(() => {
          return false;
        });
    }
  }
};
</script>
<style lang="scss">
@import '../../assets/styles/variable';

.siderbar-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: $siderbarWidth;
  z-index: 11;
  background-color: $siderbarBackgroundColor;
  transition: all 0.3s ease-in-out;

  .logo-wrapper {
    height: 40px;
    line-height: 40px;
    padding: 16px 0;
    text-align: center;
    font-size: 24px;
    color: #ffffff;
    color: #65cea7;
  }

  .el-badge {
    position: absolute;
    right: 200px;
  }
  .menu-wrapper {
    position: absolute;
    top: 72px;
    bottom: 0;
    width: 100%;
    border-right: none;
    transition: all 0.3s ease-in-out;

    &:not(.el-menu--collapse) {
      overflow-y: auto;
      overflow-x: hidden;
    }

    i {
      color: #ffffff;
    }

    .menu-item {
      &.is-active,
      &:hover {
        background-color: #353f4f !important;
        color: #65cea7 !important;

        i {
          color: #65cea7 !important;
        }
      }
    }

    .el-submenu__title:hover {
      color: #65cea7 !important;

      i {
        color: #65cea7 !important;
      }
    }

    .el-submenu,
    .el-menu-item {
      width: 100%;
    }
  }
}

.topbar-wrapper {
  position: fixed;
  left: $siderbarWidth;
  right: 0;
  top: 0;
  height: $topbarHeight;
  line-height: $topbarHeight;
  padding: 0 24px 0 0;
  background-color: $topbarBackgroundColor;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
  transition: all 0.3s ease-in-out;
  z-index: 12;
  display: flex;
  justify-content: space-between;

  .menu-collapse-wrapper {
    height: 100%;
    width: 48px;
    text-align: center;
    cursor: pointer;

    i {
      transition: all 0.3s ease-in-out;
    }
  }

  .title {
    height: 100%;
    font-weight: bold;
  }

  .menu-list {
    .menu-item {
      position: relative;
      float: left;
      padding: 0 15px;
      min-width: 45px;
      height: 48px;
      text-align: center;
      font-size: 0px;

      &:hover {
        cursor: pointer;
        background-color: #f5f5f5;
      }

      .icon {
        vertical-align: middle;
        font-size: 24px;
      }

      .text {
        display: inline-block;
        vertical-align: middle;
        margin-left: 4px;
        font-size: 16px;
      }
    }
  }
}

.btn_left {
  display: flex;
  align-items: center;
  margin-right: 7px;
  cursor: pointer;
}
.btn_left .item {
  margin-right: 10px;
}

.content-wrapper {
  position: fixed;
  left: $siderbarWidth;
  right: 0;
  top: $topbarHeight;
  bottom: 0;
  padding: 16px;
  overflow: auto;
  transition: all 0.3s ease-in-out;

  .content {
    position: relative;
    width: 100%;
    height: 100%;
  }
}
</style>



